import { Component, OnInit, AfterViewInit,ChangeDetectorRef,AfterContentInit  } from '@angular/core';
import {Http, Headers, Response} from '@angular/http';
import { DatePipe } from '@angular/common';
import {MainService} from "../../main.service";
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-search-vendor-bill',
  templateUrl: './search-vendor-bill.component.html',
  providers: [DatePipe],
  styleUrls: ['./search-vendor-bill.component.css']
})
export class SearchVendorBillComponent implements OnInit {

  vendorData;
  nameIndex = "";
  nameIndexSplit;
  searchData;
  paidAmount=0;
  addAmount=0;
  cashBills = 0;
  lastSelectedName="";
  totalAmt=0;
  startDate;
  endDate;
  inputStartDate;
  inputEndDate;
  type;
  todaySysDate;
  lastNilBalRecord;
  balanceZeroNotFound =false;

  getEmployeesName(){
    this.http.get("http://localhost:3000/api/getVendorData")
    .map(res=>res.json())
    .subscribe(
        resp=>this.vendorData = resp
      );
  }
  
  getBillsReport(type){

    if(type=="auto"){
      let tmpTodaysDate =  new Date();
      this.endDate = this.datepipe.transform(tmpTodaysDate, 'yyyy-MM-dd');
      tmpTodaysDate.setDate(tmpTodaysDate.getDate()-30);
      this.startDate = this.datepipe.transform(tmpTodaysDate, 'yyyy-MM-dd');
      this.inputEndDate ="";
      this.inputStartDate ="";

    }else{
      this.endDate = this.datepipe.transform(this.inputEndDate, 'yyyy-MM-dd');
      this.startDate = this.datepipe.transform(this.inputStartDate, 'yyyy-MM-dd');
      this.type=type;
    }
    
    this.lastSelectedName = this.nameIndex;
    this.nameIndexSplit = this.nameIndex.split(' ');
    let tmpeName=this.nameIndexSplit[0];
    let index = parseInt(tmpeName);

    this.http.get("http://localhost:3000/api/searchVendorBills?vID="+this.vendorData[index-1].vID+"&startDate="+this.startDate+"&endDate="+this.endDate)
    .map(res=>res.json())
    .subscribe(resp=>{
      this.searchData=resp;
      if(this.searchData.length==0)
        alert("No Data Found!");
      this.calculateAdvance();
    });
  }
  calculateAdvance(){
    this.paidAmount=0;
    this.addAmount=0;
    this.cashBills = 0;

    let minusNo=0;
    if(this.type!="manual" && !this.balanceZeroNotFound)
      minusNo=1;
      
      for(let i=0;i<this.searchData.length-minusNo;i++){
      if(this.searchData[i].tranType=="Paid")
        this.paidAmount+=this.searchData[i].amount;
      else if (this.searchData[i].tranType=="Add")
        this.addAmount+=this.searchData[i].amount;
      else if (this.searchData[i].tranType=="Cash")
        this.cashBills+=this.searchData[i].amount;
    }    
  }
  getAmountNCalcTotal(amt){
    this.totalAmt+=amt;
    return amt;
  }

  editVendorBill(vendorBillObj){
    this.MainSer.setEditMode(true);
    this.MainSer.setEditObj(vendorBillObj);
    this.router.navigate(['vendorAddPaidBill'], {});
  }

  fGetLastNilBalanceRecord(){
    this.type ="auto";
    
    this.lastSelectedName = this.nameIndex;
    this.nameIndexSplit = this.nameIndex.split(' ');
    let tmpeName=this.nameIndexSplit[0];
    let index = parseInt(tmpeName);

    this.http.get("http://localhost:3000/api/getVendorLastNilBalRecord?vID="+this.vendorData[index-1].vID)
        .map(res=>res.json())
        .subscribe(resp=>{ 
          if(resp.length!=0){
            this.balanceZeroNotFound =false;
            this.lastNilBalRecord=resp[0]; 
            this.fGetDueRecord();
          }else{
            this.balanceZeroNotFound =true;
            this.http.get("http://localhost:3000/api/getVendorLastNilBalRecordBalZeroNotFound?vID="+this.vendorData[index-1].vID)
            .map(res=>res.json())
            .subscribe(resp=>{ 
              if(resp.length!=0){              
              this.lastNilBalRecord=resp[0]; 
              this.fGetDueRecord();
              }else{
                this.searchData=resp; 
                this.calculateAdvance(); 
                alert("No Record Found!");
              }
            });

          }
          
        });
  }

  fGetDueRecord(){

    this.http.get("http://localhost:3000/api/getVendorDueRecords?vID="+this.lastNilBalRecord.vID+"&id="+this.lastNilBalRecord.id)
        .map(res=>res.json())
        .subscribe(resp=>{ 
          this.searchData=resp;    
          this.calculateAdvance(); 
          if(this.searchData.length==0){
            alert("No Record Found!");
          } 
        });

  }

  constructor(private http: Http,
     private cdref: ChangeDetectorRef, 
     public datepipe: DatePipe,
     public MainSer: MainService,
     public router: Router) { 

    this.cdref.detach();
  }

  ngOnInit() {
    let tmptodaySysDate = new Date();
    this.todaySysDate = this.datepipe.transform(tmptodaySysDate, 'yyyy-MM-dd');
    this.getEmployeesName();
  }

  ngAfterContentChecked () {
    this.cdref.detectChanges();
  }

}
