import { Component, OnInit,ChangeDetectorRef } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import {Http} from '@angular/http';
import { DatePipe } from '@angular/common';
@Component({
  selector: 'app-pav-report',
  templateUrl: './pav-report.component.html',
  providers: [DatePipe],
  styleUrls: ['./pav-report.component.css']
})
export class PavReportComponent implements OnInit {
  public searchString: string;
  public pavData=[];
  orderPav=0;
  returnPav=0;
  paidPav=0;
  usedPav =0;
  unpaidPav =0;
  pavRate = 37;
  startDate;
  endDate;
  inputStartDate;
  inputEndDate;
  type;
  todaySysDate;
  lastNilBalRecord;
  getPavData(type){
    this.resetPavCount();
    if(type=="auto"){
      let tmpTodaysDate =  new Date();
      this.endDate = this.datepipe.transform(tmpTodaysDate, 'yyyy-MM-dd');
      tmpTodaysDate.setDate(tmpTodaysDate.getDate()-30);
      this.startDate = this.datepipe.transform(tmpTodaysDate, 'yyyy-MM-dd');
    }else{
      this.endDate = this.datepipe.transform(this.inputEndDate, 'yyyy-MM-dd');
      this.startDate = this.datepipe.transform(this.inputStartDate, 'yyyy-MM-dd');
      this.type = type;
    }
    

      this.http.get("http://localhost:3000/api/getPavData?startDate="+this.startDate+"&endDate="+this.endDate)
        .map(res=>res.json())
        .subscribe(resp=>{
          this.pavData=resp;    
          console.log("pavData "+this.pavData)         
          this.pavCalculation(); 
          if(this.pavData.length==0){
            alert("No Data Found!");
          }     
        });

  }

  fGetLastNilBalanceRecord(){
    this.http.get("http://localhost:3000/api/getLastNilBalRecord")
        .map(res=>res.json())
        .subscribe(resp=>{ 
          if(resp.length!=0){
            this.lastNilBalRecord=resp[0]; 
            this.fGetDueRecord();
          }
          
        });
  }

  fGetDueRecord(){

    this.http.get("http://localhost:3000/api/getDueRecords?pno="+this.lastNilBalRecord.pno)
        .map(res=>res.json())
        .subscribe(resp=>{ 
          this.pavData=resp;    
          console.log("pavData "+this.pavData)         
          this.pavCalculation(); 
          if(this.pavData.length==0){
            alert("No Data Found!");
          } 
        });

  }

  resetPavCount(){
    this.orderPav=0;
    this.returnPav=0;
    this.paidPav=0;
    this.usedPav =0;
    this.unpaidPav =0;
  }
  pavCalculation(){
    let minusNo=0;
    if(this.type!="manual")
      minusNo=1;

   for(let i=0;i<this.pavData.length-minusNo;i++){
    this.orderPav+=this.pavData[i].orderedPav;
    this.returnPav+=this.pavData[i].returnPav;
    this.paidPav+=this.pavData[i].paidPav;
   }

   this.usedPav= this.orderPav - this.returnPav;
   this.unpaidPav= this.usedPav - this.paidPav;


  }
  constructor(private http: Http,private router: Router, private cdref: ChangeDetectorRef
    ,public datepipe: DatePipe) {
    this.cdref.detach();
   }

  ngOnInit() {
    //this.getPavData("auto");
    this.fGetLastNilBalanceRecord()
    let tmptodaySysDate = new Date();
    this.todaySysDate = this.datepipe.transform(tmptodaySysDate, 'yyyy-MM-dd');
  }
  ngAfterContentChecked () {
    this.cdref.detectChanges();
  }
}
