import { Component, OnInit,  } from '@angular/core';
import {Http, Headers, Response} from '@angular/http';
import { Router, ActivatedRoute } from '@angular/router';
import { DatePipe } from '@angular/common';

@Component({
  selector: 'app-customer-bills',
  templateUrl: './customer-bills.component.html',
  providers: [DatePipe],
  styleUrls: ['./customer-bills.component.css']
})
export class CustomerBillsComponent implements OnInit {
  public searchString: string;
  public cBillData=[];
  billDate;  
  items;
  cname="";
  todaySysDate;

  constructor(private http: Http, public datepipe: DatePipe) { }

  ngOnInit() {
    let tmptodaySysDate = new Date();
    this.todaySysDate = this.datepipe.transform(tmptodaySysDate, 'yyyy-MM-dd');
    this.billDate = this.datepipe.transform(tmptodaySysDate, 'yyyy-MM-dd');
    this.getItemsData();
  }

  
  getCustomerBill(){
    let tmpDate = this.datepipe.transform(this.billDate, 'MM-dd-yyyy');
      this.http.get("http://localhost:3000/api/getCustBill?billDate="+tmpDate+"&cname="+this.cname)
        .map(res=>res.json())
        .subscribe(resp=>{
          this.cBillData=resp;    
          console.log("Bill Data "+this.cBillData);
          
          if(this.cBillData.length==0){
            alert("No Record Found!");
          }
        });

  }

  getItemsData()
  {
    if(localStorage.getItem('menu')==null){
      this.http.get("http://localhost:3000/api/GetItemsData")
      .map(res=>res.json())
      .subscribe(resp=>this.items=resp);
    }else{
      this.items = JSON.parse(localStorage.getItem('menu'));
    }
  }

}
