import { Component, OnInit, AfterViewInit,ChangeDetectorRef,AfterContentInit  } from '@angular/core';
import { FormControl, FormGroup, FormBuilder, Validators } from '@angular/forms';
import {Http, Headers, Response} from '@angular/http';
import { DatePipe } from '@angular/common';
import {MainService} from "../../main.service";
import { Router, ActivatedRoute } from '@angular/router';
@Component({
  selector: 'app-search-employee-advance',
  templateUrl: './search-employee-advance.component.html',
  providers: [DatePipe],
  styleUrls: ['./search-employee-advance.component.css']
})
export class SearchEmployeeAdvanceComponent implements OnInit    {

  empData;
  empNameIndex = "";
  empNameIndexSplit;
  empSearchData;
  paidAmount=0;
  receivedAmount=0;
  lastSelectedEmpName="";
  totalAmt=0;
  startDate;
  endDate;
  inputStartDate;
  inputEndDate;
  type;
  todaySysDate;
  lastNilBalRecord;
  balanceZeroNotFound = false;
  getEmployeesName(){
    this.http.get("http://localhost:3000/api/getEmpData")
    .map(res=>res.json())
    .subscribe(resp=>this.empData=resp);
  }
  
  getAdvanceReport(type){

    if(type=="auto"){
      let tmpTodaysDate =  new Date();
      this.endDate = this.datepipe.transform(tmpTodaysDate, 'yyyy-MM-dd');
      tmpTodaysDate.setDate(tmpTodaysDate.getDate()-30);
      this.startDate = this.datepipe.transform(tmpTodaysDate, 'yyyy-MM-dd');
      this.inputEndDate ="";
      this.inputStartDate ="";

    }else{
      this.endDate = this.datepipe.transform(this.inputEndDate, 'yyyy-MM-dd');
      this.startDate = this.datepipe.transform(this.inputStartDate, 'yyyy-MM-dd');
      this.type=type;
    }
    
    this.lastSelectedEmpName = this.empNameIndex;
    this.empNameIndexSplit = this.empNameIndex.split(' ');
    let tmpeName=this.empNameIndexSplit[0];
    let index = parseInt(tmpeName);

    this.http.get("http://localhost:3000/api/searchEmpAdvance?eno="+this.empData[index-1].eno+"&startDate="+this.startDate+"&endDate="+this.endDate)
    .map(res=>res.json())
    .subscribe(resp=>{
      this.empSearchData=resp;
      if(this.empSearchData.length==0)
        alert("No Data Found!");
      this.calculateAdvance();
    });
  }

  fGetLastNilBalanceRecord(){
    this.type ="auto";
    this.lastSelectedEmpName = this.empNameIndex;
    this.empNameIndexSplit = this.empNameIndex.split(' ');
    let tmpeName=this.empNameIndexSplit[0];
    let index = parseInt(tmpeName);

    this.http.get("http://localhost:3000/api/getEmpLastNilBalRecord?eno="+this.empData[index-1].eno)
        .map(res=>res.json())
        .subscribe(resp=>{ 
          if(resp.length!=0){
            this.balanceZeroNotFound =false;
            this.lastNilBalRecord=resp[0]; 
            this.fGetDueRecord();
          }else{
            this.balanceZeroNotFound =true;
            this.http.get("http://localhost:3000/api/getEmpLastNilBalRecordBalZeroNotFound?eno="+this.empData[index-1].eno)
            .map(res=>res.json())
            .subscribe(resp=>{ 
              if(resp.length!=0){              
              this.lastNilBalRecord=resp[0]; 
              this.fGetDueRecord();
              }else{
                this.empSearchData=resp; 
                this.calculateAdvance(); 
                alert("No Record Found!");
              }
            });

          }
          
        });
  }

  fGetDueRecord(){

    this.http.get("http://localhost:3000/api/getEmpDueRecords?eno="+this.lastNilBalRecord.eno+"&id="+this.lastNilBalRecord.id)
        .map(res=>res.json())
        .subscribe(resp=>{ 
          this.empSearchData=resp;    
          this.calculateAdvance(); 
          if(this.empSearchData.length==0){
            alert("No Record Found!");
          } 
        });

  }

  calculateAdvance(){
    let minusNo=0;
    if(this.type!="manual" && !this.balanceZeroNotFound)
      minusNo=1;

    this.paidAmount=0;
    this.receivedAmount=0;
    console.log("calculateAdvance")
    for(let i=0;i<this.empSearchData.length-minusNo;i++){
      if(this.empSearchData[i].tranType=="Paid")
        this.paidAmount+=this.empSearchData[i].amount;
      else
        this.receivedAmount+=this.empSearchData[i].amount;
    }
    
    //return this.paidAmount-this.receivedAmount;
  }
  getAmountNCalcTotal(amt){
    this.totalAmt+=amt;
    return amt;
  }

  editEmployeeAdvance(empAdvObj){
    this.MainSer.setEmpAdvEditMode(true);
    this.MainSer.setEmployeeAdvObj(empAdvObj);
    this.router.navigate(['employeesAdvance'], {});
  }

  constructor(private http: Http,
     private cdref: ChangeDetectorRef, 
     public datepipe: DatePipe,
     public MainSer: MainService,
     public router: Router) { 

    this.cdref.detach();
  }

  ngOnInit() {
    let tmptodaySysDate = new Date();
    this.todaySysDate = this.datepipe.transform(tmptodaySysDate, 'yyyy-MM-dd');
    this.getEmployeesName();
  }

  ngAfterContentChecked () {
    this.cdref.detectChanges();
  }

}
