import { Component, OnInit, AfterViewInit,ChangeDetectorRef,AfterContentInit  } from '@angular/core';
import {Http, Headers, Response} from '@angular/http';
import { DatePipe } from '@angular/common';
import {MainService} from "../../main.service";
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-search-cust-bills',
  templateUrl: './search-cust-bills.component.html',
  providers: [DatePipe],
  styleUrls: ['./search-cust-bills.component.css']
})
export class SearchCustBillsComponent implements OnInit {

  custData;
  nameIndex = "";
  nameIndexSplit;
  searchData;
  paidAmount=0;
  addAmount=0;
  cashBills = 0;
  lastSelectedName="";
  totalAmt=0;
  startDate;
  endDate;
  inputStartDate;
  inputEndDate;
  type;
  todaySysDate;
  lastNilBalRecord;
  balanceZeroNotFound =false;
  monthNames = ["January", "February", "March", "April", "May", "June",
                "July", "August", "September", "October", "November", "December"
                ];
  yearlyDataObj = [];  
  yearReportFlag = false;
  selectedYear;
  addQty = 0;
  paidQty = 0;
  cashQty = 0;

  getCustomersName(){
    this.http.get("http://localhost:3000/api/getFarsanCustData")
    .map(res=>res.json())
    .subscribe(
        resp=>this.custData = resp
      );
  }

  getYearlyReport(){
    this.yearReportFlag = true
    this.selectedYear = this.datepipe.transform(this.inputStartDate, 'yyyy');

    this.lastSelectedName = this.nameIndex;
    this.nameIndexSplit = this.nameIndex.split(' ');
    let tmpeName=this.nameIndexSplit[0];
    let index = parseInt(tmpeName);
    let yearSearchData;

    this.http.get("http://localhost:3000/api/getYearlyProductsSale?cno="+this.custData[index-1].cno+"&year="+this.selectedYear)
    .map(res=>res.json())
    .subscribe(resp=>{
      yearSearchData=resp;
      console.log(yearSearchData);
      
      for(let i=0;i<12;i++){
        let obj = yearSearchData.filter(item => {
          const date = new Date(item.packeddate); 
          return date.getMonth() === i;
        });
        this.yearlyDataObj[i] = obj.reduce((total, obj) => obj.qty + total,0)
      }
      

      console.log(this.yearlyDataObj);

      if(yearSearchData.length==0)
        alert("No Data Found!");     
    });
  }
  
  getBillsReport(type){

    if(type=="auto"){
      let tmpTodaysDate =  new Date();
      this.endDate = this.datepipe.transform(tmpTodaysDate, 'yyyy-MM-dd');
      tmpTodaysDate.setDate(tmpTodaysDate.getDate()-30);
      this.startDate = this.datepipe.transform(tmpTodaysDate, 'yyyy-MM-dd');
      this.inputEndDate ="";
      this.inputStartDate ="";

    }else{
      this.yearReportFlag = false;
      this.endDate = this.datepipe.transform(this.inputEndDate, 'yyyy-MM-dd');
      this.startDate = this.datepipe.transform(this.inputStartDate, 'yyyy-MM-dd');
      this.type=type;
    }
    
    this.lastSelectedName = this.nameIndex;
    this.nameIndexSplit = this.nameIndex.split(' ');
    let tmpeName=this.nameIndexSplit[0];
    let index = parseInt(tmpeName);

    this.http.get("http://localhost:3000/api/searchFarsanCustBills?cno="+this.custData[index-1].cno+"&startDate="+this.startDate+"&endDate="+this.endDate)
    .map(res=>res.json())
    .subscribe(resp=>{
      this.searchData=resp;
      if(this.searchData.length==0)
        alert("No Data Found!");
      this.calculateAdvance();
    });
  }
  calculateAdvance(){
    this.paidAmount=0;
    this.addAmount=0;
    this.cashBills = 0;

    this.addQty = 0;
    this.paidQty = 0;
    this.cashQty = 0;

    let minusNo=0;
    if(this.type!="manual" && !this.balanceZeroNotFound)
      minusNo=1;
      
      for(let i=0;i<this.searchData.length-minusNo;i++){
      if(this.searchData[i].trantype=="Paid"){
        this.paidAmount+=this.searchData[i].totalBill;
        this.paidQty +=this.searchData[i].qty;
      }        
      else if (this.searchData[i].trantype=="Add"){
        this.addAmount+=this.searchData[i].totalBill;
        this.addQty +=this.searchData[i].qty;
      }       
      else if (this.searchData[i].trantype=="Cash"){
        this.cashBills+=this.searchData[i].totalBill;
        this.cashQty +=this.searchData[i].qty;
      }
        
    }    
  }
  getAmountNCalcTotal(amt){
    this.totalAmt+=amt;
    return amt;
  }

  editVendorBill(vendorBillObj){
    this.MainSer.setEditMode(true);
    this.MainSer.setEditObj(vendorBillObj);
    this.router.navigate(['vendorAddPaidBill'], {});
  }

  getYearlyProductsSale(){
    return this.yearlyDataObj.reduce((a, b) => a + b, 0)
  }
  

  fGetLastNilBalanceRecord(){
    this.type ="auto";
    this.yearReportFlag = false;
    this.lastSelectedName = this.nameIndex;
    this.nameIndexSplit = this.nameIndex.split(' ');
    let tmpeName=this.nameIndexSplit[0];
    let index = parseInt(tmpeName);

    this.http.get("http://localhost:3000/api/getCustBillLastNilBalRecord?cno="+this.custData[index-1].cno)
        .map(res=>res.json())
        .subscribe(resp=>{ 
          if(resp.length!=0){
            this.balanceZeroNotFound =false;
            this.lastNilBalRecord=resp[0]; 
            this.fGetDueRecord();
          }else{
            this.balanceZeroNotFound =true;
            this.http.get("http://localhost:3000/api/getCustBillLastNilBalRecordBalZeroNotFound?cno="+this.custData[index-1].cno)
            .map(res=>res.json())
            .subscribe(resp=>{ 
              if(resp.length!=0){              
              this.lastNilBalRecord=resp[0]; 
              this.fGetDueRecord();
              }else{
                this.searchData=resp; 
                this.calculateAdvance(); 
                alert("No Record Found!");
              }
            });

          }
          
        });
  }

  fGetDueRecord(){

    this.http.get("http://localhost:3000/api/getFarsanCustDueRecords?cno="+this.lastNilBalRecord.cno+"&id="+this.lastNilBalRecord.id)
        .map(res=>res.json())
        .subscribe(resp=>{ 
          this.searchData=resp;    
          this.calculateAdvance(); 
          if(this.searchData.length==0){
            alert("No Record Found!");
          } 
        });

  }

  constructor(private http: Http,
     private cdref: ChangeDetectorRef, 
     public datepipe: DatePipe,
     public MainSer: MainService,
     public router: Router) { 

    this.cdref.detach();
  }

  ngOnInit() {
    let tmptodaySysDate = new Date();
    this.todaySysDate = this.datepipe.transform(tmptodaySysDate, 'yyyy-MM-dd');
    this.getCustomersName();
  }

  ngAfterContentChecked () {
    this.cdref.detectChanges();
  }

}
