 addBill()
 {
   this.getCNote();
   for(let i=0;i<this.bill.length;i++)
   {
     var json = JSON.stringify({itemno: parseInt(this.bill[i].itemno)+1, qty: this.bill[i].qty, billno: this.billno, date: this.latest_date,  time: this.latest_time,  cname: this.cname, note: this.cNote, foodstatus: 'preparing', tableno:500, 'mobileno' :this.cMobile})
     var header = new Headers();
     header.append("Content-Type", "application/json");
     this.http.post("http://"+this.MainSer.IPAddress+":3000/api/add", json, {headers: header})
     .map(res=>res.json())
     .subscribe(
       res => {          
        // console.log("success"+res);
         /* if(i==this.bill.length-1)
           this.printBillSuccess(); */
       },
       error => { 
         if(i==this.bill.length-1)                             
           alert("Please Check Command Promt");
       }
     ) 
       
   }
 }
