copyMessage(val: string){
 const selBox = document.createElement('textarea');
 selBox.style.position = 'fixed';
 selBox.style.left = '0';
 selBox.style.top = '0';
 selBox.style.opacity = '0';
 selBox.value = val;
 document.body.appendChild(selBox);
 //selBox.focus();
 selBox.select();
 document.execCommand('copy');
 document.body.removeChild(selBox);
}

getItemsData()
{
 if(localStorage.getItem('menu')==null){
   this.http.get("http://"+this.MainSer.IPAddress+"/api/GetItemsData")
   .map(res=>res.json())
   .subscribe(resp=>this.items=resp);
 }else{
   this.items = JSON.parse(localStorage.getItem('menu'));
 }
}


getBillNo()
{
 this.todayDate =  new Date(); 
  this.latest_date =this.datepipe.transform(this.todayDate, 'MM-dd-yyyy');
  this.latest_time =this.datepipe.transform(this.todayDate, 'shortTime');
 
 this.http.get("http://"+this.MainSer.IPAddress+":3000/api/billno?date="+this.latest_date)
   .map(res=>res.json())
   .subscribe(resp=>{
     this.billno=resp;
     //this.setBillNo()
    // console.log(this.billno)
     if(this.billno!="")
       this.billno=this.billno[0].billno+1
     else
       this.billno=1

       this.addBill()
   });    
}

