 constructor(private http: Http, public datepipe: DatePipe,
   public MainSer: MainService,
   public router: Router,
   public cdr: ChangeDetectorRef) { 
   this.branchName=this.MainSer.getbranchName();  
  // console.log("----------"+this.items?.length);
   if(this.MainSer.autoIP==true){
     this.MainSer.IPAddress = window.location.hostname;
     this.MainSer.getItemsData().subscribe(resp=>this.items=resp);
   }else{
     this.MainSer.IPAddress = this.MainSer.staticIPAddress;
     this.MainSer.getItemsData().subscribe(resp=>this.items=resp);
   }

  this.subscription1 = this.MainSer.mangerMode.subscribe(value => {
         this.managerMode=value; 
         
         if(this.managerMode)
           this.loginBtnText ="Sign Out";
                                           
  });

 this.subscription2 = this.MainSer.ownerMode.subscribe(value => {
         this.ownerMode=value;        
         
         if(this.ownerMode)
           this.loginBtnText ="Sign Out";
                                           
 });

 this.subscription3 = this.MainSer.bhukumShopMode.subscribe(value => {
         this.bhukumShopMode=value;        
         
         if(this.bhukumShopMode)
           this.loginBtnText ="Sign Out";
                                           
 });



 }

 ngOnInit() {
  // this.MainSer.setLoginMode("manager",false);
  // this.MainSer.setLoginMode("owner",false);
   if(this.bhukumShopMode == false){
     this.MainSer.setLoginMode("manager",true);
     this.sendDB_BackupMail();
    //alert(this.bhukumShopMode);
   }
   this.setAdminMode();
 }

 ngOnDestroy() {
   if(this.subscription1)
    this.subscription1.unsubscribe();

   if(this.subscription2)
    this.subscription2.unsubscribe();

    if(this.subscription3)
    this.subscription3.unsubscribe();
 }

