import { Component, OnInit } from '@angular/core';
import {MainService} from "../../main.service";
import { Router } from '@angular/router';
import {Http, Headers, Response} from '@angular/http';

@Component({
  selector: 'app-order-billing-select-table',
  templateUrl: './order-billing-select-table.component.html',
  styleUrls: ['./order-billing-select-table.component.css']
})
export class OrderBillingSelectTableComponent implements OnInit {

  waitername = "Rohit";
  unpaidOrders = [];
  unpaidOrdersObj = [];
  unpaidOrdersObjKeys = [];
  tableStatus = [];
  timer;
  constructor(
    public MainSer: MainService,
    public router: Router,
    private http: Http
    ) { }

  ngOnInit() {
    //this.waitername = prompt("Please enter your name");
    console.log(this.waitername);
    this.getAllUnpaidOrder();
    this.timer = setInterval(() => {
      this.getAllUnpaidOrder(); 
    }, 40000);
  }

  getAllUnpaidOrder(){
    this.http.get("http://"+this.MainSer.IPAddress+":3000/api/getAllUnpaidOrders")
    .map(res=>res.json())
    .subscribe(resp=>{
      this.unpaidOrders=resp; 
      this.unpaidOrdersObj=this.MainSer.groupArrayOfObjects(this.unpaidOrders,"tableno"); 
      this.unpaidOrdersObjKeys = Object.keys(this.unpaidOrdersObj)    

      //console.log(this.unpaidOrdersObj[1]);
      this.setTableFoodStatus();
     // console.log("Food Status"+this.unpaidOrdersObj[1].find(x => x.foodstatus === 'preparing'))
  
    }); 
  }
  setTableFoodStatus(){
    for(let i=0;i<this.MainSer.totalTables;i++){
      if(this.unpaidOrdersObj[i+1]==undefined){
        this.tableStatus[i]="empty";
      }
      else{
          if(this.unpaidOrdersObj[i+1].find(x => x.foodstatus === 'preparing')){
            this.tableStatus[i]="preparing";
          }else{
            this.tableStatus[i]="ready";
          }
      }
    }
  }
  
  
  goToOrderDetails(tableNo){
    console.log(tableNo);
    this.MainSer.setTableNo(tableNo);
    this.MainSer.currentTableFoodStatus = this.tableStatus[tableNo-1];
    this.router.navigate(['tableBilling'], {});
  }

  ngOnDestroy() {
    if (this.timer) {
      clearInterval(this.timer);
    }
  }

}
