import { Component, OnInit } from '@angular/core';
import {Http, Headers, Response} from '@angular/http';
import {MainService} from "../../main.service";
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-bill-menu',
  templateUrl: './bill-menu.component.html',
  styleUrls: ['./bill-menu.component.css']
})
export class BillMenuComponent implements OnInit {
  billMenu=[];

  getBillingMenu(){
    if(localStorage.getItem('menu')==null){
      this.http.get("http://localhost:3000/api/GetItemsData")
      .map(res=>res.json())
      .subscribe(resp=>this.billMenu=resp);
    }else{
      this.billMenu = JSON.parse(localStorage.getItem('menu'));
    }
  }

  addBillingMenu(){
    this.MainSer.setCreateMode(true);
    this.router.navigate(['addBillingMenu'], {});
  }

  editBillingMenu(editObj){
    this.MainSer.setEditMode(true);
    this.MainSer.setEditObj(editObj);
    this.router.navigate(['addBillingMenu'], {});
  }
  constructor(private http: Http,
    public router: Router,
    public MainSer: MainService
    ) { }

  ngOnInit() {
    this.getBillingMenu();
  }

}
