import { Component,ChangeDetectorRef, OnDestroy  } from '@angular/core';
import {MainService} from "./main.service";
import {Http,Headers, Response} from '@angular/http';
import 'rxjs/add/operator/map';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: [
    './app.component.css',
    './w3.css'
    ]
})
export class AppComponent implements OnDestroy {
  title = 'app';
  lastClick=1
  currentClick
  tabId=1;
  managerMode=false;
  ownerMode=false;
  bhukumShopMode=false;
  subscription1: Subscription;
  subscription2: Subscription;
  subscription3: Subscription;
  menuClick(id)
  {
    this.tabId = id;
  }
 
  constructor(private http: Http, public MainSer: MainService, private cdref: ChangeDetectorRef) { 
    this.subscription1 = this.MainSer.mangerMode.subscribe(value => {
            this.managerMode=value;  
    });

    this.subscription2 = this.MainSer.ownerMode.subscribe(value => { 
            this.ownerMode=value;
    });

    this.subscription3 = this.MainSer.bhukumShopMode.subscribe(value => {
            this.bhukumShopMode=value;
    });
    
    this.cdref.detach();
  }
  ngOnInit() {
    this.MainSer.loadBeepAudio();
    this.MainSer.loadWaiterBeepAudio();
  }

  ngOnChange() {
  }
  ngAfterContentChecked () {
    this.cdref.detectChanges();
  }

  ngOnDestroy() {
    if(this.subscription1)
     this.subscription1.unsubscribe();

    if(this.subscription2)
     this.subscription2.unsubscribe();

    if(this.subscription3)
     this.subscription3.unsubscribe();
  }

}
