import { Component, OnInit } from '@angular/core';
import { addCalculation } from './addCalculation.model';
import {MainService} from "../../main.service";
import {Http,Headers, Response} from '@angular/http';
import 'rxjs/add/operator/map';
import { DatePipe } from '@angular/common';
import { Router, ActivatedRoute } from '@angular/router';
@Component({
  selector: 'app-addcalculation',
  templateUrl: './addcalculation.component.html',
  providers: [DatePipe],
  styleUrls: ['./addcalculation.component.css']
})
export class AddcalculationComponent implements OnInit {

  addCalculationObj = new addCalculation();
  branchName;
  report=[];
  report1Amount=0;
  items=[]; 
  todayDate;
  saleCalDate;
  calType;
  todaySysDate;
  getTotal(){
    let tmpTotal=(this.addCalculationObj.NotesNo_2000*2000)+(this.addCalculationObj.NotesNo_500*500)+(this.addCalculationObj.NotesNo_200*200)+(this.addCalculationObj.NotesNo_100*100)+(this.addCalculationObj.NotesNo_50*50)+(this.addCalculationObj.NotesNo_20*20)+(this.addCalculationObj.NotesNo_10*10)
    return tmpTotal
  }
  netTotal(){
    this.setAddCalculationObject()
    return this.getTotal()+this.addCalculationObj.PayTMAmount+this.addCalculationObj.ZomatoAmount+this.addCalculationObj.SwiggyAmount+this.addCalculationObj.BhimAmount+this.addCalculationObj.expences-this.addCalculationObj.change
  }

  setAddCalculationObject(){
    this.MainSer.setAddCalculationObject(this.addCalculationObj);
  }

  getShopTotal(){
    return this.getTotal()+ this.addCalculationObj.PayTMAmount + this.addCalculationObj.BhimAmount + this.addCalculationObj.expences - this.addCalculationObj.change;
  }


  getReport()
  {
  
    let tmpTodaysDate;
    
      tmpTodaysDate =  new Date();
      this.todayDate= this.datepipe.transform(tmpTodaysDate, 'MM-dd-yyyy');
    
      let tmp,i;
      this.report=[]

      for(i=1;i<=this.items.length;i++)
      {
              this.http.get("http://localhost:3000/api/todaysReport?itemno="+i+"&todaysDate="+this.todayDate)
              .map(res=>res.json())
              .subscribe(resp=>{
                tmp=resp;
                if(tmp[0].qty==null)
                  tmp[0].qty=0
                else
                  this.report.push(tmp[0])
              });
      } 

  }

  getItemsData()
  {
    if(localStorage.getItem('menu')==null){
      this.http.get("http://localhost:3000/api/GetItemsData")
      .map(res=>res.json())
      .subscribe(resp=>{this.items=resp;this.getReport();});
    }else{
      this.items = JSON.parse(localStorage.getItem('menu'));
      this.getReport();
    }
  }

  getLaptopTotal()
  {
    if(this.calType=="manual"){
      return this.addCalculationObj.laptop;
    }else{
      this.report1Amount=0;    
      for(let i = 0; i < this.report.length; i++){
        
        this.report1Amount += (parseInt(this.report[i].price) * parseInt(this.report[i].qty));
      }  
      return this.report1Amount
    }
    
      
  }
 
  saveCalculationReport(){
    let tmpTodaysDate;
    if(this.calType=="manual"){
    }else{
      tmpTodaysDate =  new Date();
    this.todayDate= this.datepipe.transform(tmpTodaysDate, 'yyyy-MM-dd');
    }
       
    
    this.http.get("http://localhost:3000/api/saleCalDate?cDate="+this.todayDate)
              .map(res=>res.json())
              .subscribe(resp=>{
                if(resp.length==0)
                  this.addNewSaleCalculation();
                else
                  this.updateSaleCalculation();
              }); 
  }
  addNewSaleCalculation(){
    let tmpTodaysDate; 
    if(this.calType!="manual"){
      tmpTodaysDate =  new Date();
      this.todayDate= this.datepipe.transform(tmpTodaysDate, 'yyyy-MM-dd-');
    }   
    


    var json = JSON.stringify({sDate: this.todayDate, 
      c2000: this.addCalculationObj.NotesNo_2000, c500: this.addCalculationObj.NotesNo_500,
      c200: this.addCalculationObj.NotesNo_200, c100: this.addCalculationObj.NotesNo_100,
      c50: this.addCalculationObj.NotesNo_50, c20: this.addCalculationObj.NotesNo_20,
      c10: this.addCalculationObj.NotesNo_10, cash: this.getTotal(),
      paytm: this.addCalculationObj.PayTMAmount, bhim: this.addCalculationObj.BhimAmount,
      sChange: this.addCalculationObj.change, shopSale: this.getShopTotal(),
      laptopSale: this.getLaptopTotal(), zomato: this.addCalculationObj.ZomatoAmount,
      swiggy: this.addCalculationObj.SwiggyAmount , expences: this.addCalculationObj.expences})
        var header = new Headers();
        header.append("Content-Type", "application/json");
        this.http.post("http://localhost:3000/api/addSaleCalculation", json, {headers: header})
        .map(res=>res.json())
        .subscribe(resp=>{
          alert("Sale Calculation Save Successfully")
        }); 

  }
  updateSaleCalculation(){
    var json = JSON.stringify({ sDate: this.todayDate, 
      c2000: this.addCalculationObj.NotesNo_2000, c500: this.addCalculationObj.NotesNo_500,
      c200: this.addCalculationObj.NotesNo_200, c100: this.addCalculationObj.NotesNo_100,
      c50: this.addCalculationObj.NotesNo_50, c20: this.addCalculationObj.NotesNo_20,
      c10: this.addCalculationObj.NotesNo_10, cash: this.getTotal(),
      paytm: this.addCalculationObj.PayTMAmount, bhim: this.addCalculationObj.BhimAmount,
      sChange: this.addCalculationObj.change, shopSale: this.getShopTotal(),
      laptopSale: this.getLaptopTotal(), zomato: this.addCalculationObj.ZomatoAmount,
      swiggy: this.addCalculationObj.SwiggyAmount, expences: this.addCalculationObj.expences})
    var header = new Headers();
    header.append("Content-Type", "application/json");
    this.http.post("http://localhost:3000/api/updateSaleCalculation", json, {headers: header})
    .map(res=>res.json())
    .subscribe(resp=>{
      alert("Sale Calculation Updated Successfully")
    });
  }  

  getChangeAmount(){
    let tmpTodaysDate;    
    tmpTodaysDate =  new Date();
    tmpTodaysDate.setDate(tmpTodaysDate.getDate()-1);
    this.todayDate= this.datepipe.transform(tmpTodaysDate, 'yyyy-MM-dd');
    console.log("todayDate"+this.todayDate);
    this.http.get("http://localhost:3000/api/saleCalDate?cDate="+this.todayDate)
              .map(res=>res.json())
              .subscribe(resp=>{
                if(resp.length==0)
                  this.addCalculationObj.change=0;
                else
                  this.addCalculationObj.change = (parseInt(resp[0].c50)*50) + (parseInt(resp[0].c20)*20) + (parseInt(resp[0].c10)*10);
              }); 
  }
  
  getLastSavedValues(type,tmpTodaysDate){
    if(tmpTodaysDate){
      this.calType = type;;
      this.todayDate= this.datepipe.transform(tmpTodaysDate, 'yyyy-MM-dd');
      console.log("todayDate"+this.todayDate);
      this.http.get("http://localhost:3000/api/saleCalDate?cDate="+this.todayDate)
              .map(res=>res.json())
              .subscribe(resp=>{
                if(resp.length!=0){
                  this.addCalculationObj.NotesNo_2000 =  resp[0].c2000;
                  this.addCalculationObj.NotesNo_500 =  resp[0].c500;
                  this.addCalculationObj.NotesNo_200 =  resp[0].c200;
                  this.addCalculationObj.NotesNo_100 =  resp[0].c100;
                  this.addCalculationObj.NotesNo_50 =  resp[0].c50;
                  this.addCalculationObj.NotesNo_20 =  resp[0].c20;
                  this.addCalculationObj.NotesNo_10 =  resp[0].c10;

                  this.addCalculationObj.PayTMAmount = resp[0].paytm;
                  this.addCalculationObj.BhimAmount = resp[0].bhim;
                  this.addCalculationObj.expences = resp[0].expences;
                  this.addCalculationObj.ZomatoAmount = resp[0].zomato;
                  this.addCalculationObj.SwiggyAmount = resp[0].swiggy;
                  this.addCalculationObj.change = resp[0].sChange;
                  if(type=="manual"){
                    
                    this.addCalculationObj.laptop = resp[0].laptopSale;
                  }
                }else{
                  if(type=="manual")
                    alert("No Record Found!");
                }
                  
              }); 
    }
    
  }

  constructor(private http: Http, public datepipe: DatePipe,public MainSer: MainService,private router: Router,) { 
    this.branchName=MainSer.getbranchName();
    this.getItemsData();
    this.getChangeAmount();
    let tmpTodaysDate;    
    tmpTodaysDate =  new Date();
    this.getLastSavedValues("auto",tmpTodaysDate);
  }

  ngOnInit() {
   // this.addCalculationObj = this.MainSer.getAddCalculationObject();
    let tmptodaySysDate = new Date();
    this.todaySysDate = this.datepipe.transform(tmptodaySysDate, 'yyyy-MM-dd');
  }

}
