addNoteInBill(){

  setTimeout(()=>{                           
    this.getCNote();
    this.bill[this.bill.length-1].note = this.cNote;
  }, 500);
}



  constructor(private http: Http, public datepipe: DatePipe,
    public MainSer: MainService,
    public router: Router,    
    public cdr: ChangeDetectorRef) { 
      
   //.subscribe( this.items = items);
    this.branchName=this.MainSer.getbranchName();
    this.todayDate =  new Date(); 
    this.latest_date = this.datepipe.transform(this.todayDate, 'MM-dd-yyyy');
    if(localStorage.getItem('menu')==null){
      this.MainSer.getItemsData().subscribe(resp=>{
        this.items=resp;
        localStorage.setItem("menu",JSON.stringify(this.items));
        this.getBillForTable();
      });
    }else{
      this.items = JSON.parse(localStorage.getItem('menu'));
      this.getBillForTable();
    }
    
    

    //this.getItemsData()
    
    
   // console.log(this.items)

   this.subscription1 = this.MainSer.mangerMode.subscribe(value => {
          this.managerMode=value;          
          if(this.managerMode)
            this.loginBtnText ="Sign Out";
                                            
  });

  this.subscription2 = this.MainSer.ownerMode.subscribe(value => {  
          this.ownerMode=value;                  
          if(this.ownerMode)
            this.loginBtnText ="Sign Out";
                                            
  });

  this.subscription3 = this.MainSer.bhukumShopMode.subscribe(value => {
          this.bhukumShopMode=value;        
          
          if(this.bhukumShopMode)
            this.loginBtnText ="Sign Out";
                                            
  });

  }

  ngOnInit() {
  }

  getBillForTable(){
    this.http.get("http://"+this.MainSer.IPAddress+":3000/api/getBillForTable?tableNo="+this.MainSer.getTableNo())
    .map(res=>res.json())
    .subscribe(resp=>{
      this.tableBill=resp; 
      this.getBillNo();
      if(this.tableBill.length){
        this.cname = this.tableBill[0].cname;
      }
      this.cdr.detectChanges();
    }); 
  }

  ngOnDestroy() {
    if(this.subscription1)
     this.subscription1.unsubscribe();

    if(this.subscription2)
     this.subscription2.unsubscribe();

     if(this.subscription3)
     this.subscription3.unsubscribe();

     if (this.timer) {
      clearInterval(this.timer);
     }
  }

